<template>
  <div class="header">
    <a class="title" href="#">
      <p class="big-title">豆芽仔.Pen</p>
      <p class="small-title">邱品的个人博客</p>
    </a>
    <nav>
      <ul class="nav">
        <router-link to="/" tag="li" exact>首页</router-link>
        <router-link to="/liveArticlePage" tag="li" class="toggle-show" exact>
          <span>生活笔记</span>
          <ul>
            <li v-for="liName in liveLis" @click="toLive(liName)">{{liName}}</li>
          </ul>
        </router-link>
        <router-link to="/itArticlePage" tag="li" class="toggle-show" exact>
          <span>技术博客</span>
          <ul>
            <li v-for="liName in itLis" @click="toIt(liName)">{{liName}}</li>
          </ul>
        </router-link>
        <router-link to="/add" tag="li" exact>写博客</router-link>
        <router-link to="/about" tag="li" exact>关于博主</router-link>
        <router-link to="/emailToMe" tag="li" exact>欢迎交流</router-link>
      </ul>
    </nav>
  </div>
</template>

<script>
  import Util from '../util.js'

  export default {
    name: "NavHeader",
    data(){
      return {
        liveLis:['讲个笑话','情感类','生活类'],
        itLis:['前端','javascript','html']
      }
    },
    methods:{
      toLive(li){
        Util.$emit('getLiveLi',li)
      },
      toIt(li){
        Util.$emit('getItLi',li)
      }
    }
  }
</script>

<style scoped>
  .title{
    display: flex;
    justify-content: center;
    align-items: baseline;
    color: #fff;
    padding: 20px;
    letter-spacing: 1px;
    text-decoration: none;
    margin-bottom: 10px;
  }
  .big-title{
    margin-right: 10px;
    font-size: 40px;
    transition: letter-spacing 1s;
  }
  .title:hover .big-title{
    letter-spacing: 10px;
  }
  .title:hover .small-title{
    opacity: 0.5;
    margin-left: 20px;
  }
  .small-title{
    transition: opacity 1s,margin-left 2s;
  }
  .header{
    background: #d56464;
  }
  nav{
    background-color: #4a4a4a;
    opacity: 0.9;
    position: relative;
    z-index: 999;
  }
  .nav{
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    max-width: 1000px;
  }
  .nav li{
    display: inline-block;
    list-style: none;
    padding: 15px 25px;
    margin-left: 1px;
    cursor: pointer;
    color: #fff;
  }
  .nav li:hover{
    background-color: #d56464;
  }
  .toggle-show{
    position: relative;
  }
  .toggle-show>ul{
    position: absolute;
    left: -2px;
    top: 51px;
    visibility:hidden
  }
  .toggle-show>ul>li{
    width: 80px;
    background-color: #4a4a4a;
    opacity: 0;
    transform: rotateY(180deg);
    transition: transform 2s,opacity 2s;
  }
  .toggle-show:hover ul{
    visibility:visible;
  }
  .toggle-show:hover>ul>li{
    transform: rotateY(360deg);
    opacity: 0.9;
  }

  .router-link-active{
    background-color: #d56464;
  }
</style>



