    .el-collapse{
      margin: 10px 0;
      opacity: 0.9;
    }
    .el-collapse-item__header{
      padding: 14px;
    }
    .time {
      font-size: 13px;
      color: #999;
    }
    .master{
      font-size: 13px;
      color: #999;
    }
    .bottom {
      margin-top: 13px;
      line-height: 12px;
    }
    .button {
      padding: 0;
      float: right;
    }
    .image {
      width: 100%;
      display: block;
    }
    .clearfix:before,
    .clearfix:after {
      display: table;
      content: "";
    }
    .clearfix:after {
      clear: both
    }
    .header-content{/*通知栏*/
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 20px;
      background-color: #fbfbfb;
      svg{
        position: relative;
        top:3px;
      }
      .left{

      }
      .right{
        display: flex;
        .prise{
          margin-left: 20px;
        }
      }
    }
    .icon-list{
      background-color: #fff;
      padding: 10px;
      a{
        margin-left: 20px;
      }
    }
    .el-collapse-item__wrap{
      border:none;
    }
    .el-collapse-item__content{
      padding-bottom: 0;
    }
    .el-row[data-v-47323bf2]{
      margin-bottom: 10px;
    }
    .show-blog{
      border-radius: 4px;
    }
    .el-carousel__item{
      display: flex;
      align-items: center;
    }
    .el-carousel__item a,img{
      border-radius: 4px;
      width: 100%;
      height: 100%;
    }
    .el-carousel__item:nth-child(2n) {
      background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
      background-color: #d3dce6;
    }
    .el-row {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .el-col {
      border-radius: 4px;
    }
    .bg-purple-dark {
      background: #99a9bf;
    }
    .bg-purple {
      background: #d3dce6;
    }
    .bg-purple-light {
      background: #e5e9f2;
    }
    .grid-content {
      border-radius: 4px;
      min-height: 36px;
    }
    .row-bg {
      padding: 10px 0;
      background-color: #f9fafc;
    }
  }

</style>




