<template>
    <div class="send-email">
      <h1 class="title">
        发现有问题或者想和我说些什么的，可以在下方留言，我会邮箱回复你的哦！
      </h1>
      <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
        <el-form-item
          prop="email"
          label="邮箱"
          :rules="[
      { required: true, message: '请输入邮箱地址', trigger: 'blur' },
      { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
    ]"
        >
          <el-input v-model="dynamicValidateForm.email"></el-input>
        </el-form-item>
        <el-form-item
          label="发现问题">
          <el-input
            type="textarea"
            :rows="6"
            placeholder="给点建议吧..."
            v-model="dynamicValidateForm.content">
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('dynamicValidateForm')">提交</el-button>
          <el-button @click="resetForm('dynamicValidateForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
    export default {
        name: "emailToMe",
      data() {
        return {
          dynamicValidateForm: {
            email: '',
            content:''
          }
        };
      },
      methods: {
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              const query = Bmob.Query('msgTome');
              query.set("email",this.dynamicValidateForm.email);
              query.set("content",this.dynamicValidateForm.content);
              query.save().then(res => {
                alert('发送成功！我看到马上回复你！')
                this.dynamicValidateForm={}
              }).catch(err => {
                console.log(err)
              })
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        }
      }

    }
</script>

<style lang="scss" scoped>
.send-email{
  max-width: 1200px;
  margin: 10px auto;
  .title{
    padding-top: 50px;
    padding-bottom: 50px;
    text-align: center;
    color: #666;
  }
  .demo-dynamic{
    width: 600px;
  }
}
</style>



