<template>
    <div id="add-blog">
      <div v-if="isMe">
        <h2>写文章(后台管理)</h2>
        <form v-if="!submitted">
          <div>
            <label>主题</label>
            <input type="text" v-model="blog.title">
          </div>
          <div>
            <label>内容</label>
            <textarea v-model="blog.content"></textarea>
          </div>
          <div id="checkboxes">
            <!--<label>Vue.js<input type="checkbox" value="Vue.js" v-model="blog.categories"></label>-->
            <p>标签：</p>
            <el-checkbox-group
              v-model="blog.categories"
              :max="3">
              <el-checkbox v-for="tag in tags" :label="tag" :key="tag">{{tag}}</el-checkbox>
            </el-checkbox-group>
          </div>
          <div>
            <p>作者：</p>
            <el-select v-model="blog.author" placeholder="请选择">
              <el-option
                v-for="author in authors"
                :key="author.value"
                :label="author.label"
                :value="author.value">
              </el-option>
            </el-select>
          </div>
          <div class="chooseImg">
            <p>选择文章封面图片：</p>
            <el-select v-model="blog.imgSrc" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <button @click.prevent="postTo">发布博客</button>
        </form>
        <div v-if="submitted">
          <h3>恭喜你，发布成功！</h3>
          <button @click="oneMore">再写一篇</button>
          <router-link to="/" tag="button">博客总览</router-link>
        </div>
        <hr>

        <div id="pre">
          <h3>博客预览</h3>
          <div><h4>主题:</h4><p>{{blog.title}}</p></div>
          <div><h4>内容:</h4><p>{{blog.content}}</p></div>
          <div><h4>分类:</h4><p v-for="category in blog.categories">{{category}}</p></div>
          <div><h4>作者:</h4><p>{{blog.author}}</p></div>
        </div>
      </div>
      <h1 v-if="!isMe">您无权访问改板块，请访问其他板块吧！</h1>
    </div>
</template>

<script>
    export default {
        name: "AddBlog",
      data(){
          return {
            isMe:false,
            tags:['javascript','html','css','生活随笔','看世界','享生活','谈感悟','技术类','生活类','讲个笑话','情感类','前端'],
            authors:[
              {
                value:'豆芽仔',
                label:'豆芽仔'
              },
              {
                value:'豆芽',
                label:'豆芽'
              }
            ],
            options: [{
              value: require('../assets/img/one.jpg'),
              label: '封面1'
            }, {
              value: require('../assets/img/two.jpg'),
              label: '封面2'
            }, {
              value: require('../assets/img/three.jpg'),
              label: '封面3'
            }, {
              value: require('../assets/img/four.jpg'),
              label: '封面4'
            }, {
              value: require('../assets/img/five.jpg'),
              label: '封面5'
            },{
              value: require('../assets/img/big-bg.jpg'),
              label: '默认封面'
            }],
            blog:{
              title:"",
              content:"",
              categories:[],
              author:"",
              imgSrc: '',
              time:''
            },
            submitted:false
          }
      },
      methods:{
        /**
         * 格式化时间
         * @param {Date} v
         * @param {String} format —— 时间的格式模式
         * @returns
         */
         dateFormat (v, format) {
          format = format || 'yyyy-MM-dd HH:mm:ss';
          if (!(v instanceof Date)) {
            v = new Date(v);
          }
          var year = v.getFullYear();
          var month = v.getMonth() + 1;
          if (month < 10) month = '0' + month;
          var date = v.getDate();
          if (date < 10) date = '0' + date;
          var hour = v.getHours();
          if (hour < 10) hour = '0' + hour;
          var minute = v.getMinutes();
          if (minute < 10) minute = '0' + minute;
          var second = v.getSeconds();
          if (second < 10) second = '0' + second;
          return format.replace('yyyy', year).replace('MM', month).replace('dd', date).replace('HH', hour).replace('mm', minute).replace('ss', second);
        },
        //发文章
        postTo:function () {
          if (this.blog.title.length === 0 || this.blog.content.length === 0 || this.blog.author.length === 0) {
            alert('题目或内容或作者不能为空');
            return
          }
           if (this.blog.categories.length === 0) {
             alert('至少选择一种标签');
             return
           }
           if (this.blog.imgSrc.length === 0) {
             this.blog.imgSrc = require('../assets/img/big-bg.jpg')
           }
          let newTime = new Date();
          // console.log(this.dateFormat(newTime))
          this.blog.time = this.dateFormat(newTime);
          console.log(this.blog);
          const query = Bmob.Query('blogs');
          query.set("blog",this.blog);
          query.save().then(res => {
              console.log(res);
              this.submitted = !this.submitted
            }).catch(err => {
              console.log(err)
            })
            // this.$http.post("http://jsonplaceholder.typicode.com/posts",{
            //   title:this.blog.title,
            //   body:this.blog.content,
            //   userId:3
            // }).then(function (res) {
            //     console.log(res);
            //
            // })
          },
        oneMore(){
           this.submitted = !this.submitted;
           this.blog = {
             title:"",
             content:"",
             categories:[],
             author:"",
             imgSrc: '',
             time:''
           }
        },
        //获取本地缓存
        getLocalStroage(){
          let storage = localStorage.getItem("bmob");
          console.log(storage);
          return storage;
        },
      },
      mounted(){
        let userMsg = JSON.parse(this.getLocalStroage());
        if (userMsg) {
          if (userMsg.phone === '15702098518'){
            this.isMe = true
          } else {
            this.isMe = false
          }
        }
      }
    }
</script>

<style scoped>
#add-blog *{
  box-sizing: border-box;
}
  #add-blog{
    max-width: 600px;
    margin: 10px auto;
    padding: 20px;
  }
  #add-blog div{
    margin-top: 10px;
  }
  h2 {

  }
  label{
    margin-bottom: 10px;
    display: inline-block;
  }
  input[type='text'],textarea{
    display: block;
    padding: 10px;
    width: 100%;
    outline: none;
    box-sizing: border-box;
  }
  textarea{
    height: 300px;
  }
  #checkboxes label{
    margin-right: 10px;
  }
  select{
    display: inline-block;
    width: 200px;
  }
  button{
    background: crimson;
    padding: 10px;
    color: #fff;
    border: 0;
    border-radius: 4px;
    font-size: 16px;
    margin-top: 10px;
    cursor: pointer;
  }
</style>



