import ShowBlogs from "./components/ShowBlogs";
import AddBlog from "./components/AddBlog";
import SingleBlog from "./components/SingleBlog";
import index from "./components/index";
import liveArticlePage from "./components/liveArticlePage";
import itArticlePage from "./components/itArticlePage";
import about from "./components/about";
import emailToMe from "./components/emailToMe";

const routes = [
  {path:"/",component:index},
  {path:"/add",component:AddBlog},
  {path:"/blog/:id",component:SingleBlog},
  {path:"/ShowBlogs",component:ShowBlogs},
  {path:"/liveArticlePage",component:liveArticlePage},
  {path:"/itArticlePage",component:itArticlePage},
  {path:"/about",component:about},
  {path:"/emailToMe",component:emailToMe},
]

export default routes;





