<template>
    <div id="show-blogs">
      <h1 class="title" v-if="!isArticlePage">文章列表</h1>
      <!--<input type="text" placeholder="搜索" v-model="search">-->
      <el-input
        placeholder="请输入内容"
        v-model="search"
        clearable>
      </el-input>
      <!--标签区-->
      <el-tag
        class="show-tag"
        v-for="item in tag"
        closable
        type=""
        @close="handleClose">
        {{item}}
      </el-tag>
      <!--单条文章-->
      <div class="single-blog" v-for="blogMsg in filteredBlogs.slice((currentPage-1)*pagesize,currentPage*pagesize)">
        <!--文章题目-->
        <router-link :to="'blog/'+blogMsg.objectId" class="title">
          <div class="title-tag" v-for="tag in blogMsg.blog.categories">{{tag}}</div>
          <h3 class="article-list-title">{{blogMsg.blog.title | snippetTitle}}</h3>
        </router-link>
        <!--文章内容-->
        <div class="article-list-content">
          <div class="article-list-content-img">
            <router-link :to="'blog/'+blogMsg.objectId">
              <img :src="blogMsg.blog.imgSrc" alt="" class="content-img">
            </router-link>
          </div>
          <div class="article-list-content-text">
            {{blogMsg.blog.content | snippet}}
          </div>
        </div>
        <!--文章底部-->
        <div class="article-list-content-bottom">
          <div class="user-name"><svg t="1572962598526" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2157" width="200" height="200"><path d="M489.8 542.1c117.4 0 212.6-102.5 212.6-228.9S607.2 84.3 489.8 84.3 277.2 186.8 277.2 313.2s95.2 228.9 212.6 228.9zM317.2 313.2c0-25.9 4.7-50.9 13.9-74.5 8.9-22.6 21.5-42.8 37.6-60.2 32.5-35 75.5-54.3 121-54.3s88.5 19.3 121 54.3c16.1 17.3 28.8 37.6 37.6 60.2 9.2 23.5 13.9 48.6 13.9 74.5s-4.7 50.9-13.9 74.5c-8.9 22.6-21.5 42.8-37.6 60.2-32.5 35-75.5 54.3-121 54.3s-88.5-19.3-121-54.3c-16.1-17.3-28.8-37.6-37.6-60.2-9.2-23.6-13.9-48.6-13.9-74.5zM679.9 558.6H299.7c-88.9 0-161.6 72.7-161.6 161.6v46.5c0 88.9 72.7 161.6 161.6 161.6H680c88.9 0 161.6-72.7 161.6-161.6v-46.5c-0.1-88.9-72.8-161.6-161.7-161.6z m121.6 208.1c0 32.3-12.7 62.8-35.8 85.8-23.1 23.1-53.5 35.8-85.8 35.8H299.7c-32.3 0-62.8-12.7-85.8-35.8-23.2-23-35.9-53.5-35.9-85.8v-46.5c0-32.3 12.7-62.8 35.8-85.8 23.1-23.1 53.5-35.8 85.8-35.8h380.3c32.3 0 62.8 12.7 85.8 35.8 23.1 23.1 35.8 53.5 35.8 85.8v46.5z" fill="#5ABE64" p-id="2158"></path></svg>{{blogMsg.blog.author}}</div>
          <div class="created-time"><svg t="1572962667298" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3008" width="200" height="200"><path d="M512 514m-448 0a448 448 0 1 0 896 0 448 448 0 1 0-896 0Z" fill="#69B452" p-id="3009"></path><path d="M512 512m-253.15 0a253.15 253.15 0 1 0 506.3 0 253.15 253.15 0 1 0-506.3 0Z" fill="#FFFFFF" p-id="3010"></path><path d="M512 512m-203.07 0a203.07 203.07 0 1 0 406.14 0 203.07 203.07 0 1 0-406.14 0Z" fill="#D1F0C7" p-id="3011"></path><path d="M622.12 497h-91.31a24.09 24.09 0 0 0-11.46-7.92V360.22a6 6 0 0 0-6-6h-2.81a6 6 0 0 0-6 6v128.93a24.06 24.06 0 1 0 26.19 38.08h91.49a6 6 0 0 0 6-6V503a6 6 0 0 0-6.1-6z" fill="#69B452" p-id="3012"></path></svg>{{blogMsg.blog.time}}</div>
          <div class="comment-count"><svg t="1572962708026" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3776" width="200" height="200"><path d="M847.36 107.52h-665.6c-69.12 0-125.44 56.32-125.44 125.44v401.92c0 69.12 56.32 125.44 125.44 125.44h38.4l15.36 181.76 158.72-181.76h453.12c69.12 0 125.44-56.32 125.44-125.44V232.96c0-69.12-56.32-125.44-125.44-125.44z m-563.2 376.32c-33.28 0-61.44-28.16-61.44-61.44 0-33.28 28.16-61.44 61.44-61.44 33.28 0 61.44 28.16 61.44 61.44 2.56 33.28-25.6 61.44-61.44 61.44z m230.4 0c-33.28 0-61.44-28.16-61.44-61.44 0-33.28 28.16-61.44 61.44-61.44 33.28 0 61.44 28.16 61.44 61.44 0 33.28-28.16 61.44-61.44 61.44z m227.84 0c-33.28 0-61.44-28.16-61.44-61.44 0-33.28 28.16-61.44 61.44-61.44 33.28 0 61.44 28.16 61.44 61.44 2.56 33.28-25.6 61.44-61.44 61.44z" fill="#dbdbdb" p-id="3777"></path></svg>{{blogMsg.sendMsgArray?blogMsg.sendMsgArray.length+'条评论':'0条评论'}}</div>
        </div>
      </div>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 20]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="filteredBlogs.length">    //这是显示总共有多少数据，
      </el-pagination>
    </div>
</template>

<script>
    import Util from '../util.js'

    export default {
        name: "ShowBlogs",
      props:{
        isArticlePage:{
          type:Boolean,
          default:false
        }
      },
      data(){
        return {
          currentPage:1, //初始页
          pagesize:5,    //    每页的数据
          userList: [],
          isTag:false,
          tag:[],
          tags:[],
          tagBlogs:[],
          blogs:[],
          search:"",
          size:10
        }
      },
      methods:{
        // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
          this.pagesize = size;
          console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
          this.currentPage = currentPage;
          console.log(this.currentPage)  //点击第几页
        },
        handleClose(){
          console.log(1111);
          this.tag.splice(0,1)
        },
        //获取文章内容
        getArticle(){
          const query = Bmob.Query("blogs");
          query.find().then(res => {
            this.blogs = res.reverse();
            console.log(res)
            this.blogs.forEach(item => {
              item.blog.categories.forEach(item => {
                if (this.tags.indexOf(item) === -1) {
                  this.tags.push(item)
                }
              })
            });
            localStorage.setItem("tags", this.tags);
          });
          // this.$http.get("http://jsonplaceholder.typicode.com/posts").then(function (res) {
          //   // console.log(res)
          //   this.blogs = res.body.slice(0,10);
          // })
        }

      },
      //监听tag的变化
      watch:{
          tag(){
            // console.log(this.tag);
            // console.log(this.blogs);
            this.tagBlogs = [];
            this.blogs.forEach(item => {
              item.blog.categories.forEach(tag => {
                if (this.tag[0] === tag) {
                  this.tagBlogs.push(item)
                }
              })
            });
            console.log(this.tagBlogs)
          }
      },
      mounted() {
        this.getArticle();
        //监听传过来的标签
        Util.$on('searchTag',tag => {
          if (this.tag.length > 0) {
            this.tag = []
          }
          this.tag.push(tag)
        });
        Util.$on('getLiveLi',tag => {
          if (this.tag.length > 0) {
            this.tag = []
          }
          this.tag.push(tag)
        });
        Util.$on('getItLi',tag => {
          if (this.tag.length > 0) {
            this.tag = []
          }
          this.tag.push(tag)
        })
      },
      //自定义过滤器
      filters:{
          "to-uppercase":function (value) {
            return value.toUpperCase()
          },
          "snippet":function (value) {
            return value.length>100?value.slice(0,100) + "...":value
          },
          "snippetTitle":function (value) {
            return value.length>10?value.slice(0,10) + "...":value
          }
      },
      //计算属性
      computed:{
          filteredBlogs:function () {
            let that = this;
            if (this.tag.length > 0) {
              return this.tagBlogs.filter(function (blogMsg) {
                return blogMsg.blog.title.match(that.search)
              })
            }else {
              return this.blogs.filter(function (blogMsg) {
                return blogMsg.blog.title.match(that.search)
              })
            }
          }
      }
    }
</script>

<style lang="scss" scoped>
#show-blogs{
  max-width: 800px;
  .show-tag{
    margin-top: 10px;
  }
  .title{
    font-size: 18px;
    color: #fff;
    padding-bottom: 10px;
  }
  .article-list-content{
    display: flex;
    justify-content: flex-start;
    .content-img{
      width: 220px;
      height: 130px;
      margin: 10px;
      position: relative;
      top: -12px;
    }
    .article-list-content-text{
      flex: 1;
      color: #777;
    }
  }
  .article-list-content-bottom{
    display: flex;
    justify-content: flex-end;
    color: #777;
    padding-bottom: 10px;
    div{
      margin-right: 20px;
    }
  }
}
  .single-blog{
    margin: 10px 0;
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 4px;
    .user-name,.created-time,.comment-count{
      display: flex;
      align-items: center;
    }
    .icon{
      width: 15px;
      height: 15px;
      margin-right: 5px;
    }
    .title{
      text-decoration: none;
      font-weight: normal;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .article-list-title{
        padding: 10px;
        font-size: 18px;
        font-weight: normal;
        color: #00a67c;
      }
      .title-tag:first-child{
        margin-left: 10px;
      }
      .title-tag{
        color: #fff;
        background-color: #d33632;
        padding: 2px 4px;
        font-size: 12px;
        margin-right: 10px;
        position: relative;
        .tag-arrow{
          display: inline-block;
          width: 0;
          height: 0;
          border: 6px solid;
          border-color: transparent transparent transparent #d33632;
          position: absolute;
          top: 5px;
          right: -12px;
        }
      }
    }
  }
  .el-pagination{
    background-color: #fff;
    border-radius: 4px;
    padding: 8px 50px;
  }
  input[type="text"]{
    display: inline-block;
    width: 100%;
    padding: 8px;
    outline: none;
    box-sizing: border-box;
  }
</style>



