          sendContent:'',
        },
        changed:false,
        blog:{},
        id:this.$route.params.id,
        blogChange:{},
        ruleForm: {},
        userMsg:{},
        userLoginForm:{},
        isLogin:false,
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
        },
        dialogTableVisible:false,
        dialogActiveName: 'userLogin',
        currentDate: '描述',
        activeName: '1',
        cardList:[]
      }
    },
    methods:{
      refreash(){
        console.log(22122);
        this.$router.go(0)
      },
      /**
       * 格式化时间
       * @param {Date} v
       * @param {String} format —— 时间的格式模式
       * @returns
       */
      dateFormat (v, format) {
        format = format || 'yyyy-MM-dd HH:mm:ss';
        if (!(v instanceof Date)) {
          v = new Date(v);
        }
        var year = v.getFullYear();
        var month = v.getMonth() + 1;
        if (month < 10) month = '0' + month;
        var date = v.getDate();
        if (date < 10) date = '0' + date;
        var hour = v.getHours();
        if (hour < 10) hour = '0' + hour;
        var minute = v.getMinutes();
        if (minute < 10) minute = '0' + minute;
        var second = v.getSeconds();
        if (second < 10) second = '0' + second;
        return format.replace('yyyy', year).replace('MM', month).replace('dd', date).replace('HH', hour).replace('mm', minute).replace('ss', second);
      },
      //发送留言
      sendMessage(){
        let newTime = new Date();
        this.haveMsg = true;
        this.sendMsg.sendTime = this.dateFormat(newTime);
        this.sendMsg.sendName = this.userMsg.username;
        // console.log(this.sendMsgArray instanceof Array);
        if (!(this.sendMsgArray instanceof Array)){
          this.sendMsgArray = [];
        }
        if (this.sendMsg.sendContent.trim()) {
          this.sendMsgArray.push(this.sendMsg);
          console.log(this.sendMsgArray);
          const query = Bmob.Query('blogs');
          query.get(this.id).then(res => {
            console.log(res);
            res.set('sendMsgArray',this.sendMsgArray);
            res.save();
            Bmob.Query('blogs').get(this.id).then(res => {
              //console.log(res)
              this.blog = res.blog;
              this.sendMsgArray = res.sendMsgArray;
              this.sendMsg.sendContent = '';
            }).catch(err => {
              console.log(err)
            })
          }).catch(err => {
            console.log(err)
          })
        }else {
          alert('留言内容不能为空')
        }
      },
      //删除
      destroy(){
        let ifDes = confirm("确定要删除该博客吗？");
        if (ifDes) {
          const query = Bmob.Query('blogs');
          query.destroy(this.id).then(res => {
            console.log(res);
            alert("删除成功");
            this.$router.push({path:"/"})
          }).catch(err => {
            console.log(err)
          })
        }
      },
      //点击编辑
      change(){
        this.changed = !this.changed;
        this.blogChange = this.blog;
      },
      //确认修改
      curChange(){
        console.log(1);
        const query = Bmob.Query('blogs');
        query.get(this.id).then(res => {
          //console.log(res)
          res.set('blog',this.blogChange);
          res.save()
        }).catch(err => {
          console.log(err)
        });
        alert("修改成功");
        this.changed = !this.changed;
      },
      //切换选项卡
      handleClick(){

      },
      //获取本地缓存
      getLocalStroage(){
        let storage = localStorage.getItem("bmob");
        console.log(storage);
        return storage;
      },
      //注册
      submitRegisterForm(formName) {
        console.log(formName);
        let params = {
          username: formName.name,
          password: formName.checkPass,
          email: formName.email,
          phone: formName.phone,
        };
        Bmob.User.register(params).then(res => {
          alert('注册成功,请去登录！');
          this.dialogActiveName = "userLogin";
          this.dialogTableVisible = false;
          console.log(res)
        }).catch(err => {
          console.log(err);
          alert('注册失败，用户已经存在')
        });
      },
      //重置
      resetForm(dataType) {
        let temp = dataType;
        this.temp = {}
      },
      //用户登录
      submitUserLoginForm(loginMsg){
        Bmob.User.login(loginMsg.name,loginMsg.pass).then(res => {
          alert('登录成功');
          this.dialogTableVisible = false;
          this.$router.go(0);
          console.log(res)
        }).catch(err => {
          alert('登录失败');
          console.log(err)
        });
      },
      //退出
      outLogin(){
        localStorage.removeItem('bmob');
        this.$router.go(0);
      },
      //获取文章内容
      getArticle(){
        Bmob.Query("blogs").find().then(res => {
          this.articleList = res.reverse();
          this.cardList = this.articleList.sort(function(){ return 0.5 - Math.random()}).slice(0,3)
          // this.bannerList = res.slice(0,size);
          // console.log(res);
        });
      }
    },

    mounted() {
      let userMsg = JSON.parse(this.getLocalStroage());
      console.log(userMsg);
      if (userMsg) {
        this.isLogin = true;
        if (userMsg.phone === '15702098518'){
          userMsg.isMe = true;
        } else {
          userMsg.isMe = false;
        }
        this.userMsg = userMsg
      }
      this.getArticle();
      Bmob.Query('blogs').get(this.id).then(res => {
        //console.log(res)
        this.blog = res.blog;
        this.sendMsgArray = res.sendMsgArray;
        if (res.sendMsgArray) {
          this.haveMsg = true
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
</script>

