<script>
  import ShowBlogs from './ShowBlogs'
  import Util from '../util.js'
  export default {
    name: "liveArticlePage",
    components:{
      ShowBlogs
    },
    data(){
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return{
        type:'',
        tags:[],
        blogs:[],
        ruleForm: {},
        userMsg:{},
        userLoginForm:{},
        isLogin:false,
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
        },
        dialogTableVisible:false,
        dialogActiveName: 'userLogin',
      }
    },
    methods:{
      //点击标签
      tagClick(tag){
        Util.$emit('searchTag',tag)
      },
      //获取最新文章列表
      getArticle(){
        Bmob.Query("blogs").find().then(res => {
          this.blogs = res.reverse().slice(0,5);
        });
      },
      //切换选项卡
      handleClick(){

      },
      //获取本地缓存
      getLocalStroage(){
        let storage = localStorage.getItem("bmob");
        console.log(storage);
        return storage;
      },
      //注册
      submitRegisterForm(formName) {
        console.log(formName);
        let params = {
          username: formName.name,
          password: formName.checkPass,
          email: formName.email,
          phone: formName.phone,
        };
        Bmob.User.register(params).then(res => {
          alert('注册成功,请去登录！');
          this.dialogActiveName = "userLogin";
          this.dialogTableVisible = false;
          console.log(res)
        }).catch(err => {
          console.log(err);
          alert('注册失败，用户已经存在')
        });
      },
      //重置
      resetForm(dataType) {
        let temp = dataType;
        this.temp = {}
      },
      //用户登录
      submitUserLoginForm(loginMsg){
        Bmob.User.login(loginMsg.name,loginMsg.pass).then(res => {
          alert('登录成功');
          this.dialogTableVisible = false;
          this.$router.go(0);
          console.log(res)
        }).catch(err => {
          alert('登录失败');
          console.log(err)
        });
      },
      //退出
      outLogin(){
        localStorage.removeItem('bmob');
        this.$router.go(0);
      },
      //获取标签缓存
      getTags(){
        localStorage.getItem('tags').split(',').forEach(item=>{
          if (Math.random() < 0.3) {
            this.type = ''
          }else if (Math.random() < 0.6) {
            this.type = 'success'
          }else if (Math.random() < 0.8) {
            this.type = 'danger'
          }else{
            this.type = 'warning'
          }
          this.tags.push({
            content:item,
            type:this.type
          })
        });
        console.log(this.tags)
      }
    },
    filters:{
      "to-uppercase":function (value) {
        return value.toUpperCase()
      },
      "snippet":function (value) {
        return value.length>10?value.slice(0,10) + "...":value
      }
    },
    mounted() {
      let userMsg = JSON.parse(this.getLocalStroage());
      console.log(userMsg);
      if (userMsg) {
        this.isLogin = true;
        if (userMsg.phone === '15702098518'){
          userMsg.isMe = true;
        } else {
          userMsg.isMe = false;
        }
        this.userMsg = userMsg
      }
      this.getArticle();
      this.getTags()
    }
  }
</script>

